@import './skins.scss';

.cover-section-2{
	background: linear-gradient(to right,$background-color-blend, transparent, $background-color-blend), 
				url(../images/camera-coffee-composition-1509428.jpg) center center;
	background-attachment: fixed;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

.go-to-card{
	margin: 8em auto;
	text-align: left;
}

.go-to-card .card-img-top{
	padding:5em;
}


.wwd-section-heading{
	color: $brand-orange;
    font-size: 40px;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 2px;
    text-align: center;
}

.wwd-section-subheading{
	color: $brand-purple;
    font-size: 30px;
}

.wwd-icon{
	padding: 5px;
    max-height: 60px;
    margin: auto;
    text-align: center;
}

.link-icon-color,
.link-icon-color:hover{
	color: #1871cd;
}

.top-cards-align{
	margin-top: 8em;
}


@media only screen and (max-width: 768px) and (min-width: 0){
	.go-to-card{
		padding-left: 5px;
		padding-right: 5px;
	}
	.wwd-section-heading{
		font-size: 32px;
	}
	
	.wwd-section-subheading{
		font-size: 24px;
	}
	.wwd-icon-wrap{
		padding: 0;
	}
}