@import './skins.scss';

.cover-section{
	/* background: linear-gradient(to right,$background-color-2, transparent, $background-color-2), url(../images/center-images.jpg) center center; */
	background: linear-gradient(to bottom, rgba(0,0,0,0) 0, $background-color-2 150%, $background-color-2 100%), 
				linear-gradient(to right,$background-color-2, transparent, $background-color-2), 
				url(../images/websiteimages/who-hero.jpg) center center;
	background-size: cover;
	/* min-height: 52em; */
}

.center-texts{
	font-size: 38px;
    text-align: center;
    padding-top: 3rem;
    padding-bottom: 3rem;
    margin: auto;
    width: 80%;
	color: white;
	@media screen and (max-width: 768px) {
		font-size: 32px;
		padding-top: 1.5rem;
		padding-bottom: 1.5rem;	
	}
	@media screen and (max-width: 600px) {
		font-size: 24px;
		padding-top: 1.5rem;
		padding-bottom: 1.5rem;	 	
	}
}


.dna-texts {
	text-align: center;
	margin: auto;
    padding-top: 3rem;
    margin: auto;
	width: 80%;
	line-height: 40px;
	color: #002A32;
	p {
		font-size: 20px;
		line-height: 30px;
		color: #002A32;
		margin: auto;
	}
	.row{
		text-align: left;
	}
}

.dna-h-separator{
	padding: 20px 0 15px;
}

.dna-h-separator::before {
    background: white;
    content: "";
    display: block;
    height: 2px;
    margin: 0 auto;
    width: 100px;
}

.dna-emphasize{
	color: mix($brand-orange, white, 75%);
	font-weight: bold;
}