.main-section{
	min-height: 56em;
}

.inline-block{
	display: inline-block;
}

.padding-bottom-20{
	padding-bottom: 20px;
}

.padding-top-bottom-10-10{
  padding-top: 10px;
	padding-bottom: 10px;
}

.padding-5{
	padding: 5px;
}

.padding-20{
	padding: 20px;
}

.center{
	text-align: center;
}

.hide-this{
	display: none;
}

.contact-us-btn{
	border-radius: 5px;
	width: fit-content;
	padding-left: 3em !important;
	padding-right: 3em !important;
	background-color: $brand-purple;
  border: 0px;
  color: $font-color-2 !important;
}

@media (max-width: 992px) {
	.nav-link {
		padding-left: 5% !important;
		padding-right: 5% !important;
	}
}

.contact-us-btn:hover,
.contact-us-btn:focus,
.contact-us-btn:active{
  background-color: $brand-orange !important;
  /* background-color: $background-color-2; */
	color: $font-color-2;
	border: 0px;
}



.front-text-block{
	margin-top: 2em;
}

.front-text-left{
	position: absolute;
  left: 5%;
}


	
.card-section{
	margin: auto;
}

.card-special:hover {
	transform: scale(1.1);
	transition: 0.3s;
	z-index: 1;
	-webkit-box-shadow: 0 0 15px 5px #1b1b1b;
    -moz-box-shadow: 0 0 15px 5px #1b1b1b;
    box-shadow: 0 0 15px 5px #1b1b1b;
}
.card-special {
  transition: 0.3s;
  height: 95%;
}

.card{
	padding: 1em;
}


.row{
	margin: 0;
}

/* simple - enter transition 300ms, exit 150ms */
.fade-appear,
.fade-enter {
    opacity: 0;
    z-index: 1;
}
.fade-appear-active,
.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 200ms linear 100ms;
}

.fade-exit {
    opacity: 1;
}

.fade-exit.fade-exit-active {
    opacity: 0;
    transition: opacity 100ms linear;
}








