@import './skins.scss';

.navbar{
	box-shadow: 0 0 !important;
}

.temp-navbar-bg{
	background-color: $background-color;
}

.temp-navbar-brand{
	padding: 0em;
}

@media screen and (min-width: 992px){
	.navbar-collapse:not(.show) .temp-nav-separator:after{
		display: list-item;
		content: "";
		background: #ddd;
		height: 1em;
		position: absolute;
		top: 50%;
		width: 1px;
		-webkit-transform: translate(0, -50%);
		-moz-transform: translate(0, -50%);
		-ms-transform: translate(0, -50%);
		-o-transform: translate(0, -50%);
		transform: translate(0, -50%);
		z-index: 1;
		margin-right: 1em;
	}
}

.temp-navbar-margin{
	padding-right: 3em;
	padding-left: 3em;
} 


@media only screen and (max-width: 768px) and (min-width: 0){
	.temp-navbar-margin {
		padding-right: 3%;
		padding-left: 0;
	}
}

.navbar-toggler:focus{
	outline: 0px;
}

.navbar-toggler:not(.collapsed){
	/* border: 1px solid #1f8fff; */
	border: 1px solid $font-color-1;
	border-radius: .25rem;
}

/* .nav-item{
	vertical-align: middle;
} */

.nav-link{
	margin-left: 3em;
	margin-right: 3em;
	/* font-weight: bold; */
	/* letter-spacing: 2pt; */
	color: $nav-link-color;
    font-size: 15px;
    font-weight: 500;
    display: inline-block;
    text-transform: capitalize;
    padding: 1em 2em;
	line-height: 1;
    transition: color 0.3s;
    text-transform: capitalize;
    line-height: 1;
	font-family: "Poppins", sans-serif;
	border-radius: 5px;
}

/* .navbar-nav li:hover{
	background-color: #f2f2f2;
} */

.navbar-collapse:not(.show) .contact-us-btn{
	margin-left: 6em;
}

@media screen and (max-width: 992px){
	.navbar-collapse {
		li{
			/* vertical-align: middle; */
			height: 45px;
		}
	}
	
}

@media screen and (max-width: 1242px){
	/* .navbar-collapse:not(.show) .contact-us-btn{
		margin-left: 1em;
	} */
	.navbar-expand-lg .navbar-nav .nav-link{
		margin-left: 1em;
		margin-right: 1em;
	}
}


.pos-relative{
	position: relative;
}


.pos-relative ul{
	margin-left: 3em;
	margin-right: 3em;
	margin-top: 0px;
    padding: 0px;
	border: 0px;
}

.dropdown-item{
	margin: 0;
	padding: 0.75em;
	/* background-color: mix($background-color-2, $background-color, 5%); */
}

.nav-link:focus, .nav-link:hover , .nav-link:active {
	background-color:  mix($background-color-2, $background-color, 10%);
}