@import './skins.scss';

.front-section{
	background: linear-gradient(to right,$background-color-blend, transparent, $background-color-blend), url(../images/websiteimages/hero.jpg) no-repeat center;
	background-size: cover;
}

@media (max-width: 992px){
	.front-section{
		background: linear-gradient(to bottom right,$background-color-blend 25%, transparent), url(../images/websiteimages/hero.jpg) no-repeat center;
		background-size: cover;
	}
}

	

.front-texts{
	color: white;
	font-size: 42px;
	line-height: 42px;
	font-family: 'Roboto', sans-serif;
	font-style: normal;
	margin: auto;
  	text-align: left;
}

.learn-more{
	color: white;
    font-size: 28px;
    line-height: 42px;
    font-family: 'Roboto', sans-serif;
	font-style: normal;
	text-align: left;
	vertical-align: middle;
    padding-left: 2em;
    padding-right: 2em;
}


.learn-more-btn{
	border-radius: 5px;
    width: fit-content;
    background-color: $brand-orange;
	border: 0px;
}

.learn-more-btn:hover,
.learn-more-btn:active,
.learn-more-btn:focus{
	color: white;
	background-color: mix(white, $brand-orange, 25%);;
    border: 0px;
}

.learn-more-btn a{
	font-size: 18px;
}
.learn-more-btn a:hover{
	color: white;
}


@media (max-width: 425px) {
	.center-mob {
		text-align: center;
	}
}
@media (min-width: 768px) {
	.center-tab {
		text-align: center;
	}
}